import { useParams } from "react-router-dom";
import { getProyects } from "../proyects";
import Navbar from "../components/navbar/Navbar";
import Footer from "../components/footer/Footer";
function Proyect (){
    const params = useParams();
    const proyect = getProyects(parseInt(params.id));
    return(
        <div>
            <Navbar />
            <div className="img1">
            <div className="bodyy">
                    <h2>Detalles</h2>
                    <div className="line"></div>
                    <div className="details">
                        <h3>Título:</h3> <p>{proyect.nombre}</p>
                        <h3>Descripcion:</h3><p>{proyect.descripcion}</p>
                        <h3>Descripcion:</h3><p>{proyect.github}</p>
                        <h3>Descripcion:</h3><p>{proyect.sweb}</p>
                    </div>
             </div>
            </div>
            <Footer/>
        </div>
    );
}
export default Proyect;