import {getAllStudies} from "../studies"
import Navbar from "../components/navbar/Navbar";
import Footer from "../components/footer/Footer";
import "../pages/css/studies.css"
import "../index.css"
import {Link} from "react-router-dom"
function Studies(){
    const studies=getAllStudies();
    return(
        <div>
            <Navbar />
            <div className="img1">
            <div className="bodyy">
                <h2>Estudios</h2>
                    <div className="line"></div>
                    <div className="cards">
                        {studies.map(study=>
                        <div key={study.id}  className="card">
                            <div className="group">
                            <div className="titulo">
                                {study.titulo}
                            </div>
                            <div className="instituto">
                                {study.institucion}
                            </div>
                            </div>
                            <div className="">
                            <div className="btn-vm">
                               <Link to={`${study.id}`} className="link">Ver más</Link>    
                            </div></div>
                        </div>
                        )}
                    </div>    
            </div>
            </div>
            <Footer/>
        </div>
    );
}
export default Studies